<template>
<div id="newbook">
  <div class="ui form" >
      <div class="field">
        <label></label>
        <input type="text" v-model="title" placeholder="30">
      </div>
      <div class="field">
        <label></label>
        <input type="text" v-model="keywords" placeholder="58">
      </div>
      <div class="field">
        <label></label>
        <input type="text" v-model="text" placeholder=':--&lt;br&gt;--&lt;img src="http:///."&gt;'>
      </div>
      <div class="ui  large  button" @click='save()'></div>
  </div>

  <div class="ui dimmer">
      <div class="content">
        <div class="center">
          <h2 class="ui inverted icon  header">
            <i class="heart icon"></i> !
          </h2>
        </div>
      </div>
    </div>
  </div>   
</div>   
</template>

<script>
import { mapActions} from 'vuex'
export default {
  data(){
    return {
    title:'',
    keywords:'',
    text:''
      
    }
  },
  methods:{
    ...mapActions(['addItem']),
   save(){
     //console.log(this.photoIdx)
     this.addItem({
         title:this.title,
         keywords:this.keywords,
         text:this.text
      })
       $('#newbook').dimmer('show')
     
   }
  }
}
</script>


